
//main styles

.main-wrapper{
	padding: 0 0 0 0;
	min-width: $min-width;
    width: 100%;
	position: relative;
	overflow: hidden;
	min-height:100%;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	flex-direction:column;
    -webkit-flex-direction:column;
    -ms-flex-direction: column;
	justify-content:flex-start;	
    -webkit-justify-content:flex-start;	
    -ms-flex-pack: start;
}
.wrapper{
	min-width: $min-width;
	max-width: $max-width;
	padding: 0 40px 0 40px;
	margin: 0 auto;
	position: relative;
}

/* titles */
p {	
	@include font($base-font-size,$base-line-height,$text-color);
	font-weight: 400;	
	line-height: 170%;
}
h1 {	
	@include font($font-size-h1,$line-height-h1,$title-color);
	font-family: 'Tenor Sans';
	text-transform: capitalize;
	font-weight: 400;	
}
h2 {
	@include font($font-size-h2,$line-height-h2,$title-color);	
	font-family: 'Tenor Sans';
	text-transform: capitalize;
	font-weight: 400;
}
h3 {
	@include font($font-size-h3,$line-height-h3,$title-color);
	font-weight: 400;
	font-family: 'Tenor Sans';
	text-transform: capitalize;
}
h4 {
	font-size: 32px;
	line-height: 130%;
	font-family: 'Tenor Sans';
	text-transform: capitalize;
	font-weight: 400;
}

h5, .title5 {
	font-size: 24px;
	line-height: 100%;
	font-family: 'Tenor Sans';
	text-transform: capitalize;
	font-weight: 400;
	color: #222222;
}

h6, .title6 {
	font-size: 20px;
	line-height: 120%;
	font-family: 'Tenor Sans';
	text-transform: capitalize;
	font-weight: 400;
	color: #222222;
}

/* text position */
.text-left{text-align: left!important}
.text-center{text-align: center!important}
.text-right{text-align: right!important}
.nowrap{white-space: nowrap!important;}


		/* loader */
.loaded .main-wrapper{visibility:hidden; opacity: 0;}
.icon-load{background:url(../img/loader.gif) no-repeat left top;width:40px;height:40px;position:fixed;left:50%;top:50%;margin-left:-20px;margin-left:-20px;display:none;}
.loaded .icon-load{display:block;}


/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content{	
	min-width: $min-width;
	text-align:left;
    width: 100%;
	order: 2;
    -webkit-order: 2;
	flex-grow: 1;
    -webkit-flex-grow: 1;
}

.btn {
	height: 60px;
	line-height: 59px;
	outline: none;
	border: none;
	background: #222222;
	padding: 0 50px;
	font-weight: bold;
	font-size: 14px;
	color: #FFFFFF;
	text-transform: uppercase;
	display: inline-block;
	transition: $transition;
	font-family: 'Lato';
	&:hover {
		background: #545454;
	}
	&-icon {
		display: flex;
		align-items: center;
		i {
			margin-right: 12px;
			font-size: 17px;
		}
	}
	&-grey {
		background: #FAF9FF;
		border: 1px solid #EEEEEE;
		color: #222222;
		&:hover {
			color: #eee;
			background: #222222;
			border-color: #222222;
		}
	}
}

.saint-text {
	font-family: 'Mrs Saint Delafield';
	color: #D05278;
	display: block;
	font-size: 50px;
	text-transform: capitalize;
}

.main-text {
	display: block;
	font-family: 'Tenor Sans';
	color: #222222;
	font-size: 100px;
	margin-bottom: 30px;
	margin-top: 4px;
	span {
		color: #D05278;
		position: relative;
		&:before {
			content: '';
			position: absolute;
		    left: 45%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 181px;
			height: 155px;
			background-image: url(../img/main-text-decor.png);
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			transition: $transition;
		}
	}
}

// main block

.main-block {
	padding-top: 340px;
    padding-bottom: 237px;
	background-image: url(https://via.placeholder.com/1920x910);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	z-index: 1;
	&__content {
		margin-left: -8px;
	}
	&__decor {
		position: absolute;
		left: 0;
	    top: 36%;
    	width: 42.4%;
		z-index: -1;
	}
	.saint-text {
		font-size: 60px;
		padding-left: 9px;
	}
	.main-text {
		line-height: normal;
	}
	p {
		max-width: 465px;
		font-size: 20px;
		line-height: 150%;
		font-family: 'Tenor Sans';
		padding-left: 10px;
	}
	.btn {
		margin-top: 60px;
		margin-left: 10px;
	}
}

.box-tab-cont {
	position: relative;
}

.hide {
	position: absolute;
	left: 0;top: 0;
	height:0; width:0;
	overflow:hidden;
	z-index: -1;
	opacity: 0;
}

// trending

.trending {
	padding-top: 180px;
	padding-bottom: 130px;
	&-top {
		text-align: center;
		margin-bottom: 59px;
		p {
			max-width: 456px;
			margin: 0 auto;
			margin-top: 29px;
		}
	}
	&-tabs {
		.tabs {
			justify-content: center;
			display: flex;
			margin-bottom: 40px;
			flex-wrap: wrap;
		}
		li {
			margin: 0 7.1px;
			a {
				padding: 0 30px;
				height: 41px;
				line-height: 40px;
				background: #FAF9FF;
				border: 1px solid #EEEEEE;
				font-size: 16px;
				color: #666666;
				display: block;
				&:hover {
					opacity: .8;
				}
				@-moz-document url-prefix() {
					line-height: 38px;
				}
			}
			&.active {
				a {
					background: #D05278;
					color: #FFFFFF;
				}
			}
		}
	}
}
 
.products {
	&-items {
		padding: 0 115px;
		margin-left: -22px;
		margin-right: -22px;
		// display: flex;
	}
	&-item {
		margin: 0 15px;
		width: calc(25% - 30px);
		position: relative;
		display: block;
		&__img {
			position: relative;
			height: 570px;
			width: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__hover {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #222222;
			background-color: rgba($color: #222222, $alpha: 0.15);
			padding: 30px;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			opacity: 0;
			transition: $transition;
			.icon-search {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				font-size: 70px;
				color: #fff;
			}
			&-options {
				display: flex;
				i {
					width: 60px;
					height: 60px;
					border-radius: 50%;
					margin: 0 10px;
					display: flex;
					align-items: center;
					justify-content: center;
					background: #FFFFFF;
					box-shadow: 0px 4px 10px rgba(34, 34, 34, 0.15);
					color: #222222;
					cursor: pointer;
					transition: $transition;
					font-size: 20px;
					&.icon-cart {
						background: #D05278;
						color: #fff;
					}
					&:hover {
						opacity: .8;
					}
					&.active {
						background: #D05278;
						color: #fff;
					}
				}
			}
		}
		&:hover {
			.products-item__hover {
				opacity: 1;
			}
		}
		&__type {
			position: absolute;
			right: 0;
			top: 0;
			display: flex;
			flex-direction: column;
		}
		&__sale, &__new {
			background: #A3D2A2;
			padding: 10px 25px;
			text-transform: uppercase;
			color: #FFFFFF;
			font-weight: bold;
			font-size: 14px;
			line-height: 100%;
			z-index: 1;
		}
		&__new {
			background: rgba(208, 82, 120, 0.7);
		}
		&__info {
			text-align: center;
			margin-top: 36px;
		}
		&__name {
			font-size: 24px;
			text-transform: capitalize;
			color: #222222;
			display: block;
			margin-bottom: 9px;
			font-family: 'Tenor Sans';
			padding: 0 3px;
		}
		&__cost {
			font-weight: 700;
			font-size: 18px;
			line-height: 170%;
			color: #666666;
			display: flex;
			align-items: center;
			justify-content: center;
			span {
				color: #999999;
				font-size: 16px;
				line-height: 170%;
				text-decoration-line: line-through;
				margin-right: 10px;
				font-weight: 400;
			}
		}

	}
}

.slick-arrow {
	position: absolute;
	top: 45.5%;
	transform: translateY(-50%);
	left: 58px;
	background: #FAF9FF;
	border: 1px solid #EEEEEE;
	height: 60px;
	width: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: $transition;
	i {
		font-size: 20px;
		&:before {
			color: #999999;
		}
	}
	&:hover {
		background: #222222;
		border: 1px solid #222222;
		i {
			&:before {
				color: #fff;
			}
		}
	}
	&.slick-next {
		right: 58px;
		left: auto;
		i {
			transform: rotate(180deg);
		}
	}
	&.slick-disabled {
		pointer-events: none;
		opacity: .5;
	}
}

// main-logos

.main-logos {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-bottom: 130px;
	img {
		margin: 0 56px;
	}
}

// discount

.discount {
	padding: 214px 0;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	.wrapper {
		display: flex;
		justify-content: flex-end;
	}
	&-info {
	    max-width: 465px;
    	margin-right: 84px;
		.saint-text {
			font-size: 60px;
			padding-left: 9px;
		}
		.main-text {
			text-transform: capitalize;
		}
		p {
			font-size: 20px;
			line-height: 150%;
			font-family: 'Tenor Sans';
			padding-left: 10px;
			&.discount-info__sm {
				font-size: 16px;
				line-height: 170%;
				font-family: 'Lato';
			}
		}
		.btn {
			margin-top: 60px;
			margin-left: 10px;
		}
	}
}

// advantages

.advantages {
	padding-top: 190px;
	&-items {
		display: flex;
		justify-content: center;
		margin-left: -12px;
		margin-right: -12px;
	}
	&-item {
		margin: 0 45px;
		width: calc(33.3% - 90px);
		text-align: center;
		&__icon {
			margin-bottom: 55px;
			i {
				position: relative;
				font-size: 70px;
				&:after {
					content: '';
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					width: 110.52px;
					height: 94.85px;
					background-image: url(../img/main-text-decor.png);
					background-size: contain;
					background-repeat: no-repeat;
					background-position: center;
					transition: $transition;
				}
			}
		}
		h4 {
			margin-bottom: 6px;
		}
	}
}

// top-categories

.top-categories {
	padding-top: 184px;
	&__text {
		max-width: 456px;
		text-align: center;
		margin: 0 auto;
		margin-bottom: 56px;
		padding-left: 10px;
		p {
			margin-top: 30px;
		}
	}
	&__items {
		display: flex;
		margin-left: -15px;
		margin-right: -15px;
	}
	&__item {
		width: calc(33.3% - 30px);
		margin: 0 15px;
		position: relative;
		height: 700px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&-hover {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: rgba($color: #222222, $alpha: 0.3);
			transition: $transition;
			text-align: center;
			flex-direction: column;
			opacity: 0;
			span {
				display: block;
				font-size: 14px;
				line-height: 170%;
				text-transform: uppercase;
				color: #fff;
			}
			i {
				font-size: 50px;
			    margin-top: -15px;
			}
			h5 {
				position: relative;
				margin-bottom: 51px;
				z-index: 1;
				margin-top: 44px;
				&:after {
					content: '';
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					width: 166px;
					height: 66px;
					background-image: url(../img/top-categories-decor.png);
					background-size: contain;
					background-repeat: no-repeat;
					background-position: center;
					transition: $transition;
					z-index: -1;
				}
			}
		}
		&:hover {
			.top-categories__item-hover {
				opacity: 1;
			}
		}
	}
}

// info-blocks

.info-blocks {
	padding-top: 30px;
	&__item {
		display: flex;
		justify-content: space-between;
		position: relative;
		background-repeat: no-repeat;
		background-position: right bottom;
		background-color: #FAF9FF;
		.wrapper {
			position: initial;
			display: flex;
			justify-content: flex-end;
			width: 100%;
			align-items: flex-end;
		}
		&-reverse {
			flex-direction: row-reverse;
			background-position: left bottom;
			background-color: #FCEDEA;
			.info-blocks__item-text {
				padding: 237px 10px 223px 0;
			}
			.info-blocks__item-img {
				left: auto;
				right: 0;
			}
			.wrapper {
				justify-content: flex-start;
			}
		}
		&-img {
			width: 50%;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			display: flex;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			&-overlay {
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				background-color: rgba($color: #222222, $alpha: 0.3);
				display: flex;
				justify-content: center;
				align-items: center;
				text-align: center;
				flex-direction: column;
				z-index: 10;
				span {
					font-size: 40px;
					line-height: 140%;
					font-family: 'Tenor Sans';
					display: block;
					color: #fff;
					margin-bottom: 45px;
					text-transform: capitalize;
					margin-top: -101px;
				}
			}
			&-play {
				cursor: pointer;
				width: 150px;
				height: 150px;
				position: relative;
				transition: $transition;
				&:after {
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					width: calc(100% + 25px);
					height: calc(100% + 25px);
					border: 3px solid #FFFFFF;
					filter: blur(5px);
					content: '';
					border-radius: 50%;
				}
				&:hover {
					opacity: .8;
				}
			}
		}
		&-text {
			width: 50%;
			padding: 186px 116px;
			padding-right: 0;
			h2, p {
				max-width: 456px;
			}
			
			p {
				display: block;
				font-size: 16px;
				line-height: 170%;
				color: #666666;
				margin-top: 24px;
			}
			.btn {
				margin-top: 60px;
			}
		}
		&-descr {
			font-size: 20px;
			line-height: 150%;
			color: #666666;
			display: block;
			max-width: 456px;
			font-family: 'Tenor Sans';
			margin-top: 31px;
		}
		&-link {
			font-weight: bold;
			font-size: 14px;
			line-height: 100%;
			text-transform: uppercase;
			display: flex;
			align-items: center;
			color: #222222;
			margin-top: 35px;
			&:hover {
				opacity: .8;
			}
			.icon-video {
				font-size: 24px;
				margin-right: 20px;
			}
			.icon-arrow-lg {
			    font-size: 51px;
				margin-left: 19px;
				transition: $transition;
				&:before {
					color: #222222;
				}
			}
			&:hover {
				.icon-arrow-lg {
					margin-left: 25px;
				}
			}
		}
		iframe {
			z-index: 2;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}
}

// arrivals

.arrivals {
	padding-top: 188px;
	.trending-top {
		margin-bottom: 58px;
		padding-left: 10px;
	}
}

// latest-news

.latest-news {
	padding-top: 184px;
	&__btn {
		text-align: center;
	}
	.trending-top {
		padding-left: 10px;
	}
	.blog-items {
		margin-bottom: 0;
	}
}

.blog-items {
	display: flex;
	margin-left: -15px;
	margin-right: -15px;
	flex-wrap: wrap;
}

.blog-item {
	width: calc(50% - 30px);
	margin: 0 15px;
	margin-bottom: 60px;
	&__img {
		height: 300px;
		position: relative;
		display: block;
		transition: $transition;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&:hover {
			opacity: .8;
		}
	}
	&__date {
		position: absolute;
		top: 23px;
		right: 35px;
		z-index: 1;
		&:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 77px;
			height: 66px;
			background-image: url(../img/blog-item__date-decor.png);
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			transition: $transition;
			z-index: -1;
		}
		font-weight: bold;
		color: #222222;
		font-size: 24px;
		line-height: 24px;
		text-align: center;
		span {
			font-size: 14px;
			line-height: 100%;
			display: block;
		}
	}
	&__title {
		display: block;
		margin-top: 40px;
		font-size: 24px;
		line-height: 26px;
		color: #222222;
		font-family: 'Tenor Sans';
		margin-bottom: 15px;
		// display: -webkit-box;
		// -webkit-line-clamp: 1;
		// -webkit-box-orient: vertical;  
		// overflow: hidden;
		// text-transform: capitalize;
		&:hover {
			color: #D05278;
		}
	}
	p {
		margin-bottom: 25px;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;  
		overflow: hidden;
		width: 90%;
	}
	&__link {
		display: flex;
		align-items: center;
		font-size: 14px;
		line-height: 170%;
		text-transform: uppercase;
		width: max-content;
		i {
			font-size: 8px;
			margin-left: 10px;
			transition: $transition;
			margin-top: 1px;
		}
		&:hover {
			opacity: .8;
			i {
				margin-left: 15px;
			}
		}
	}
}

// subscribe

.subscribe {
	margin-top: 180px;
	&-form {
		background-color:#FCEDEA;
		display: flex;
		align-items: center;
		padding: 0 87px 0 60px;
		overflow: hidden;
		&__img {
			position: relative;
			z-index: 1;
			padding-top: 8px;
			margin-right: 29px;
			img {
				min-height: 50px;
				position: relative;
				display: block;
			}
			&:after {
				content: '';
				position: absolute;
				left: 47%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: 524px;
				height: 450px;
				background-image: url(../img/subscribe-img-decor.png);
				background-size: contain;
				background-repeat: no-repeat;
				background-position: center;
				transition: $transition;
				z-index: -1;
			}
		}
		form {
			z-index: 1;
			margin-top: -5px;
			width: calc(100% - 425px);
		}
		.box-field__row {
			margin-top: 30px;
			justify-content: space-between;
			.box-field {
				width: calc(100% - 186px);
			}
		}
	}
}
.box-field__row {
