				font-size: 14px;
				font-weight: 700;
				position: absolute;
				right: 0;
				bottom: -20px;
				z-index: 1;
				&:before,
				&:after {
					content: "";
					position: absolute;
					background:#222222;
				}
				&:before {
					left: 50%;
					top: 50%;
					width: 2px;
					height: 8px;
					transform: translate(-50%, -50%);
				}
				&:after {
					top: 50%;
					left: 50%;
					height: 2px;
					width: 8px;
					transform: translate(-50%, -50%);
				}
			}
			&:after {
