	border: 1px solid #EEEEEE;
	padding: 11.5px 16px;
	display: flex;
	align-items: center;
	margin-right: 19px;
}
  
.checkbox-box input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}
  
.checkmark {
	width: 15px;
	height: 15px;
	border: 1px solid #EEEEEE;
	background-color: #fff;
	margin-right: 10px;
}

.checkbox-box:hover input ~ .checkmark {
	background-color: #fff;
	transition: $transition;
}
  
.checkbox-box input:checked ~ .checkmark {
	background-color: #D05278;
}
  
.checkmark:after {
	content: "";
	position: absolute;
	display: none;
}
  
.checkbox-box input:checked ~ .checkmark:after {
	display: block;
}

