.radio-box {
	position: relative;
	padding-left: 34px;
	cursor: pointer;
	color: #222222;
	font-family: 'Tenor Sans';
	font-size: 20px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	display: flex;
	align-items: center;
	text-transform: capitalize;
	&__info {
		margin-left: 8px;
		position: relative;
		i {
			font-size: 16px;
		}
		&-content {
			position: absolute;
			opacity: 0;
			visibility: hidden;
			transition: $transition;
			padding: 27px 29px;
			padding-left: 30px;
			background: #FCEDEA;
			font-size: 12px;
			line-height: 145%;
			text-transform: initial;
			font-family: 'Lato', sans-serif; 
			color: #999999;
		    width: 250px;
			left: 43px;
			bottom: 15px;
			&:after {
				position: absolute;
				left: -8px;
				bottom: 0;
				width: 0;
				height: 0;
				border-left: 10px solid transparent;
				border-right: 10px solid transparent;
				border-top: 10px solid #FCEDEA;
				transform: rotate(180deg);
				content: '';
			}
		}
		&:hover {
			.radio-box__info-content {
				opacity: 1;
				visibility: visible;
				z-index: 11;
			}
		}
	}
}
  
.radio-box input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}
  
.radio-box .checkmark {
	position: absolute;
	top: -1px;
    left: -1px;
	width: 20px;
	height: 20px;
	background-color: #fff;
	border-radius: 50%;
	border: 2px solid #999999;
}
  
.radio-box .checkmark:after {
	content: "";
	position: absolute;
	display: none;
}
  
.radio-box input:checked ~ .checkmark:after {
	display: block;
}

.radio-box .checkmark:after {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background: #D05278;
}

